












import Vue from "vue";
import VButton from './primary.vue'
import VIcon from '@/components/icon';
import { Component, Prop } from "vue-property-decorator";
@Component({
    components:{
        VButton,
        VIcon
    }
})
export default class extends Vue {
    @Prop({required:true})
    icon!: string;

    @Prop({default:"i"})
    tag!: string;

}
