











import Vue from "vue";
import Component from "vue-class-component";
@Component
export default class extends Vue {
    active = false;

    mounted() {
        window.addEventListener("click" , this.outsideClick);
    }

    beforeUnmount() {
        window.removeEventListener("click" , this.outsideClick)   ;
    }


    outsideClick(){
            this.active = false;
        
    }
}
